<template>
  <div id = "movies">
    <h1>请选择电影</h1>
    <div class="select-bar">

      <div class="type">
        <label>类型</label>
        <ul class="select-line">
          <li v-for = "type in types" class="select-item">{{type}}</li>
        </ul>
      </div>
        
      <div class="area">
        <label>区域</label>
        <ul class="select-line">
          <li v-for= "area in areas" class="select-item">{{area}}</li>
        </ul>
      </div>

      <div class="cinema">
        <label></label>
        <ul class="select-line">
          <li v-for="cinema in cinemas" class="select-item">{{cinema}}</li>
        </ul>
      </div>

    </div>

    <div class="container">
      <div class="sort-form-select">
      </div>
      
      <hot></hot>
      <coming></coming>
      <guess></guess>
    </div>
  </div>
</template>


<script>
import hot from '../components/hot'
import coming from '../components/coming'
import guess from '../components/guess'
import photoslider from '../components/photoslider'
export default {
  name: 'movies',
  data() {
  	return {
  		username:" ",
      types: ["爱情","喜剧","冒险","恐怖","动作","其他"]
  	}
  },
  components: {
    hot,
    coming,
    guess,
    photoslider
  },
  computed: {
    getuser: function(){
      if(document.cookie.length == 0) return "";
      return this.$cookie.get("user");
    }
  },
  methods: {

  }
}

</script>

<style scoped>
 
#movies {
  width: 1000px;
  margin: 20px auto;
}
.select-line {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}


ul { 
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.select-item {
  margin: 0 5px;
  font-size: 8pt;
}
.select-item:hover {
  color: lightgrey;
  cursor: pointer;
}

</style>


