<template>
  <div id = "cinema">
    <h1>请选择影院</h1>
    <div class="select-bar">

        <div class="date">
          <label>日期</label>
          <ul class="select-line">
            <li v-for = "day in days" class="select-item">{{day}}</li>
          </ul>
        </div>
    		
        <div class="area">
          <label>区域</label>
          <ul class="select-line">
            <li v-for= "area in areas" class="select-item">{{area}}</li>
          </ul>
        </div>

        <div class="cinema">
          <label>影院</label>
          <ul class="select-line">
            <li v-for="cinema in cinemas" class="select-item">{{cinema}}</li>
          </ul>
        </div>

    </div>

    <div class="movies">
      <ul class="movies-list">
        <li v-for="movie in movies" class="movie-item">
        <img :src="movie.poster">
        <p class="movietitle">{{movie.title}}</p>
        <button @click="buyTicket(movie.title)" class="btn buy_ticket">购票</button>
      </li>
      </ul>
    </div>
  </div>
</template>



<script>
export default {
  name: 'cinema',
  data() {
  	return {
  		movies: [{
        poster: require('../assets/poster/poster1.jpg'),
        title: "海边的曼彻斯特"
      }],
      days:[],
      cinemas: [],
      areas: ["增城市","白云区","天河区","海珠区","越秀区","荔湾区","黄埔区","番禺区","花都区","南沙区","从化市"]
  	}
  },
  created: function(){
    this.getDays();
  },
  methods: {
  	getDayStr: function(addDayCount) {
    	var dd = new Date();
		  dd.setDate(dd.getDate()+addDayCount);
		  var m = dd.getMonth()+1;
		  var d = dd.getDate();
    	return m+"月"+d+"日";
    },
    buyTicket: function(title) {
      this.$router.push(`/movie/detail/${title}`);
    },
    getDays: function() {
      for(var i = 0; i < 7; i++) {
        var tDay = this.getDayStr(i);
        this.days.push(tDay);
      }
      console.log(this.days);
    }
  }
}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#cinema {
  width: 1000px;
  margin: 0 auto;
}

.select-line {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}


ul { 
  list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.select-item {
  margin: 0 5px;
  font-size: 8pt;
}
.select-item:hover {
  color: lightgrey;
  cursor: pointer;
}
.movie-item {
  position: relative;
  border: 1px #ffff8c solid;
  padding: 20px;
  text-align: center;
  margin:10px;
  width: 220px;
  height: 300px;
}
img {
  width: 10em;
}
</style>




