<template>
  <div id = "signup">
    <p class="em">{{errMsg}}</p>
    <form @submit.prevent="submit">
        <input type="email" name="email" class="email text" v-model="user.email" placeholder="邮箱（必填）" @blur="checkEmail">
        <input type="password" name="password" class="password text" v-model="user.password" placeholder="密码（必填）">
        <input type="password" name="repeated" class="repeated-password text" placeholder="重复密码">
        <input type="text" name="nickname" class="nickname text" v-model="user.nickname" placeholder="用户昵称">
        <input type="text" name="phone" class="phone text" v-model="user.phone" placeholder="手机号码">
        <div class="per">
          <input type="checkbox" id="Romance" value="Romance" v-model="user.pereference">
          <label for="Romance">爱情</label>
          <input type="checkbox" id="Comedy" value="Comedy" v-model="user.pereference">
          <label for="Comedy">喜剧</label>
          <input type="checkbox" id="Adventure" value="Adventure" v-model="user.pereference">
          <label for="Adventure">冒险</label>
          <input type="checkbox" id="Horror" value="Horror" v-model="user.pereference">
          <label for="Horror">恐怖</label>
          <input type="checkbox" id="Action" value="Action" v-model="user.pereference">
          <label for="Action">动作</label>
          <input type="checkbox" id="Others" value="Others" v-model="user.pereference">
          <label for="Others">其它</label>
        </div>
      <input class="btn" type="submit" value="提交">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signup',
  data() {
    return {
      user: {
        email: '',
        password: '',
        phone: '',
        nickname:'',
        pereference: []
      },
      vaild: false,
      errMsg: ''
    }
  },
  methods: {
    submit: function() {
        if(!this.valid) return;
        if(!this.check()) return;
        else {
          var formData = JSON.stringify(this.user);
          this.$http.post('/Register', formData)
          .then(res => {
            if (res.data == 'true') {
              console.log('注册成功');
              this.$router.push('/index');
            } else if(res.data == 'false'){
              this.errMsg="注册失败";
            }
          })
            //   console.log(res.data);
            //   console.log('注册成功');
            //   if(res.data == '用户已存在') {
            //     this.errMsg = res.data;
            //   } else if (document.cookie.length > 0) {
            //     window.location.reload();
            //     this.$router.push('/index');
            //   }
            // })
            .catch(e => {
              console.log("服务器异常！");
              console.log(e)
            })
        }
        
    },
    check: function() {
      var email = document.querySelector(".email").value;
      if (email === '') {
        this.errMsg = '邮箱不能为空';
        return false;
      } else if (document.querySelector(".password").value === '') {
        this.errMsg = '密码不能为空';
        return false;
      } else if(document.querySelector(".password").value != document.querySelector(".repeated-password").value) {
        this.errMsg = '两次密码不一致';
        return false;
      } else {
        this.errMsg = '';
        return true;
      }
      
    },
    checkEmail: function() {
      var email_ = {email: document.querySelector(".email").value};
      this.$http.post('/CheckEmail', JSON.stringify(email_))
      .then(res=>{
        if(res.data == 'true'){
          this.errMsg = '用户已存在';
          this.valid = false;
        } else if (res.data == 'false'){
          this.errMsg = '';
          this.valid = true;
        }
      })
      .catch(e => {
        console.log("服务器异常！");
        console.log(e);
      })
    }
  }
}
</script>

<style scoped>

.em {
  text-align: center;
  margin-right: 10px;
  color: red;
}
form {
  display: flex;
  flex-direction:column;
  margin: 0 auto;
}
.text, .btn{
  width: 200px;
  margin: 5px auto 5px auto;
}

.per {
  margin: 5px auto 5px auto;
  width: 170px;
  display: flex;
  flex-direction:row;
  justify-content: space-around;
  flex-wrap:wrap;
}
</style>


