 <template>
  <div id = "signin">
    <p class="msg">{{errMsg}}</p>
    <form @submit.prevent="submit">
        <input type="email" v-model="user.email" class="email" name="email" required="required" placeholder="邮箱">
        <input type="password" v-model="user.password" class="password" name="password" required="required" placeholder="密码">
        <input class="btn" type="submit" value="提交">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signin',
  data() {
    return {
      user: {
        email: '',
        password: ''
      },
      errMsg: '      '
    }
  },
  methods: {
    submit: function() {
      if(!this.check()) {
        return;
      } else {
        var formData = JSON.stringify(this.user);
        this.$http.post('/Login', formData)
        .then(res => {
            console.log('response is' + res.data);
            if(res.data == '0'){
              //用户不存在&登陆失败
              this.errMsg = "用户不存在";
            } else if (res.data == '1') {
              //密码错误&登陆错误
              this.errMsg = "密码错误";
            } else if (res.data == '2' && document.cookie.length > 0) {
              //登陆成功
              window.location.reload();
              this.$router.push('/index');
            }
          })
        .catch(e => {
          console.log("服务器异常！");
          console.log(e)
        })
      }
      
    },
    check: function() {
      if (document.querySelector('.email').value === '') {
        this.errMsg = '邮箱不能为空';
        return false;
      } else if (document.querySelector('.password').value === '') {
        this.errMsg = '密码不能为空';
        return false;
      } else {
        this.errMsg = '  ';
        return true;
      }
    }
  }
}
</script>

<style scoped>

  form {
    display: flex;
    flex-direction:column;
    margin: 0 auto;
  }
  input {
    width: 200px;
    margin: 5px auto 5px auto;
  }

  .msg {
    color: red;
    margin-bottom: 0;
  }
  
</style>


